import { HttpClient } from '@angular/common/http';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatRadioChange } from '@angular/material/radio'
import { Subscription } from 'rxjs';
import { CartProduct } from '../../shared/models/cart-product';
import { CreateOrderDto, PaymentMethod } from '../../shared/models/create-order-dto';
import States from '../../shared/models/states';
import { CartService } from '../../shared/services/cart.service';
import { CartStore } from '../../shared/stores/cart.store';

@Component({
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.css']
})
export class CheckoutComponent implements OnInit {
  public Step = Step;
  public States = States;
  public PaymentMethod = Object.keys(PaymentMethod).filter(x => isNaN(Number(x)));
  public emailRegex = /(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\[(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\])/;
  public phoneRegex = /^(\+\d{1,2}\s)?\(?\d{3}\)?([\s.-]?)\d{3}(\2)\d{4}$/;
  public cartItems: CartProduct[] = [];
  public totalCartPrice: number = 0;
  public createOrderRequest: CreateOrderDto = <CreateOrderDto>{};

  private sub: Subscription;

  constructor(private cartService: CartService,
    public cartStore: CartStore) {}

  ngOnInit() {
    this.sub = this.cartStore.$
      .subscribe(cartState => {
        this.cartItems = cartState.cartItems;
        this.totalCartPrice = this.cartItems.reduce((total, current) => total + current.totalPrice, 0);
      });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

  next(step: Step) {
    switch (step) {
      case Step.Cart:
        this.createOrderRequest.products = this.cartItems.slice();
        break;
      case Step.CustomerInfo:
      // Here is where the identity search would happen
      case Step.DeliveryInfo:
      case Step.PaymentInfo:
      // These 3 steps, the form fields are bound directly to the model, so nothing to do
        break;
      case Step.Confirmation:
        break;
      default:
        break;
    }
  }

  removeItemFromCart(index: number) {
    this.cartService.removeFromCart(index);
  }

  toggleDeliveryOption(event: MatRadioChange) {
    this.createOrderRequest.isPickup = event.value === 'PICKUP';
  }
}

export enum Step {
  Cart = 0,
  CustomerInfo = 1,
  DeliveryInfo = 2,
  PaymentInfo = 3,
  Confirmation = 4
}
