import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from '../../../environments/environment';
import { CatalogDto } from '../models/catalog-dto';
import fakeCatalog from '../models/fake-catalog';
import { AppDispatcher } from '../stores/app.dispatcher';

@Injectable({
  providedIn: 'root'
})
export class ProductsService {
  constructor(private http: HttpClient,
    private appDispatcher: AppDispatcher) {}

  public getProductCatalog() {
    // this.http.get<CatalogDto>(environment.api + '/products/catalog')
    //   .subscribe(result => {
    //     this.appDispatcher.dispatch('GET_PRODUCT_CATALOG', result);
    //   });
    const catalog = fakeCatalog;
    this.appDispatcher.dispatch('GET_PRODUCT_CATALOG', catalog);
  }
}
