import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';

@Component({
  templateUrl: './home.component.html'
})
export class HomeComponent implements OnInit {
  title = 'htx-sushibake';
  public weatherForecasts: any[];
  public weatherForecasts2: any[];

  constructor(private http: HttpClient) {

  }

  ngOnInit() {
    this.http.get<any[]>('https://localhost:5001/weatherforecast')
      .subscribe(result => {
        this.weatherForecasts = result;
      });
    this.http.get<any[]>('https://localhost:5001/weatherforecast/protected', {
      headers: { Authorization: 'Bearer ' + window.sessionStorage.getItem('token') }
    }).subscribe(result => {
      this.weatherForecasts2 = result;
    });
  }
}
