import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { AppDispatcher } from './app.dispatcher';
import { CatalogDto } from '../models/catalog-dto';

@Injectable({
  providedIn: 'root'
})
// AppStore is not the parent to the "child" stores. Figure out better name or extension here
export class ProductsStore {
  private state: ProductsState = new ProductsState();
  public $: BehaviorSubject<ProductsState>;

  private actions: string[] = [
    'GET_PRODUCT_CATALOG'
  ];

  public catalog: CatalogDto;

  constructor(private appDispatcher: AppDispatcher) {
    this.$ = new BehaviorSubject<ProductsState>(this.state);
    this.appDispatcher.register(this);
  }

  public reduce(action: string, payload: any) {
    switch(action) {
      case 'GET_PRODUCT_CATALOG':
        this.state.catalog = payload;
        break;
      default: return;
    }

    this.$.next(this.state);
  }
}

export class ProductsState {
  public catalog: CatalogDto;
}
